import { NavLink, Link } from 'react-router-dom';

export default function MainNav(){
    return(
        <div className="main-nav">
            <ul>
                <li><NavLink className="btn-menuitem" to="/firstsipmatcha/menu" end>Our Menu</NavLink></li>
                <li><NavLink className="btn-menuitem" to="/firstsipmatcha/about" end>About</NavLink></li>
            </ul>
            <Link className="btn-icon" id="ig-link" to="https://www.instagram.com/firstsipmatcha/" rel='noopener noreferrer'>
                <i className="fa fa-brands fa-instagram"></i>
            </Link>
        </div>
    );
}