import React, { createContext, useContext, useCallback, useEffect, useState, useLayoutEffect } from 'react';
import { useLocation } from 'react-router-dom';
import nprogress from 'nprogress';

// https://github.com/rstacruz/nprogress
nprogress.configure({
    minimum: 0.1,
    // easing: 'ease',
    speed: 200,
    showSpinner: false,
    parent: 'body',
});

const titleTail = ' | First Sip Matcha';
export const WindowContext = createContext();

// useContext allows functional components to access context values
export function getWindowContext() {
  return useContext(WindowContext);
}

export default function WindowProvider({ children }){
    const getVh = useCallback(() => {
        return Math.max(
            document.documentElement.clientHeight || 0,
            window.innerHeight || 0
        );
    }, []);

    const getVw = useCallback(() => {
        return Math.max(
            document.documentElement.clientWidth || 0,
            window.innerWidth || 0
        );
    }, []);

    const [clientHeight, setVh] = useState(getVh());
    const [clientWidth, setVw] = useState(getVw());

    useEffect(() => {
        const handleResize = () => {
            setVh(getVh());
            setVw(getVw());
        }
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, [getVh, getVw]);

    // SCROLL **********************************
    const [scrollY, setScrollY] = useState(false);
    useEffect(() => {
        let flag = scrollY;
        const eventScroll = () => {
            let cur = Math.max(
                document.body.scrollTop || 0,
                document.documentElement.scrollTop || 0
            );
            let newFlag = (cur > 45);
            // console.log("flag: " + flag + " | newFlag: " + newFlag)
            if (flag !== newFlag){
                flag = newFlag;
                // console.log("here!")
                setScrollY(cur);
            }
        };

        window.addEventListener("scroll", eventScroll);
        return () => {
            window.removeEventListener('scroll', eventScroll);
        };
    }, []);

    // LOADING CONTENT ***************
    const [onHome, setOnHome] = useState(false); // HOME
    const [headerHeight, updateHeaderHeight] = useState(0); // HEADER HEIGHT

    let location = useLocation();
    
    useEffect(() => {
        nprogress.start();
        window.scrollTo(0,0);
        setOnHome(location.pathname === "/firstsipmatcha");
        switch(location.pathname){
            case '/firstsipmatcha/menu':
                setOnHome(false);
                document.title = 'Our Menu' + titleTail;
                break;
            case '/firstsipmatcha/about':
                setOnHome(false);
                document.title = 'About Us' + titleTail;
                break;
            default:
                setOnHome(true);
                document.title = 'Home' + titleTail;
                break;
        }
        setTimeout(()=>{
            nprogress.done();
            (document.querySelectorAll('.loading')).forEach((el) => el.classList.remove('loading'));
            }, 50);
    }, [location.pathname]);

    return (
        <WindowContext.Provider value={{ clientHeight, clientWidth, scrollY, headerHeight, updateHeaderHeight, onHome}}>
            <div id="nprogress">
                <div className="bar" role="bar"/>
                {/* <div class="peg"/>
                </div>
                <div class="spinner" role="spinner">
                    <div class="spinner-icon"/>
                </div> */}
            </div>
            {children}
        </WindowContext.Provider>
    );
}