import { useState, useEffect, useLayoutEffect, useRef, useCallback, useContext } from 'react';
import WindowProvider, { getWindowContext } from '../contexts/WindowProvider';
import NavHeader, {getHeight} from '../components/NavHeader';
import Footer from '../components/Footer';


export default function PageLayout({ children }){
    const win = getWindowContext();

    return(
        <>
            <NavHeader
                home={win.onHome}
                vw={win.clientWidth}
                scrollY={win.scrollY}
                />
            <main className="loading">
                { children }
            </main>
            <Footer />
        </>
    );
}