// https://www.freecodecamp.org/news/react-lifecycle-methods-and-hooks-for-beginners/

// https://stackoverflow.com/a/54655508
//  - componentDidMount -> useEffect []
//      - called after component inserted into DOM
//      - great place to perform initl setup tasks (e.g. fetch data from API, setting up event listeners)
//  - componentDidUpdate -> useEffect [dependency]
//      - called after component has re-rendered due to changes in its state or props, or perform actions that should occur specifically after re-render
//      - great place to handle side effects or perform additional actions based on those changes (e.g. making network requests when props/state change, updating DOM based on state changes, interactive w/ third-party libraries after an update)
//  - componentWillUnmount -> useEffect return () [dependency]
//      - called just before component is removed from DOM
//      - perform cleanup tasks (e.g. clearing timers, unsubscribing from events, release resources to prevent memory leaks)

import { useState, useEffect, useLayoutEffect } from 'react';

var timeUntil = 0;

export default function Countdown({ targetDate }){
    const [timer, updateTimer] = useState({
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
    });

    const interval = () => {
        let distance = timeUntil - new Date().getTime();

        if (distance < 0){
            clearInterval(interval);
            updateTimer({
                days: 0, 
                hours: 0, 
                minutes: 0, 
                seconds: 0
            });
            return;
        } else{
            updateTimer({
                days: Math.floor(distance / (1000 * 60 * 60 * 24)),
                hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
                minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
                seconds: Math.floor((distance % (1000 * 60)) / 1000)
            });
        }
        setTimeout(interval, 1000);
    };

    useLayoutEffect(() => {
        targetDate = new Date(targetDate.toLocaleString('en-US', { timeZone: 'America/New_York' }));
        timeUntil = targetDate.getTime();
        (async() => {
            interval();
        })();
    }, []);

    return(
        <>
            <div className="timer-block">
                <div id="countdown-days" className="timer-block-number">{timer.days}</div>
                <div className="timer-block-value">days</div>
            </div>
            
            <div className="timer-block">
                <div id="countdown-hours" className="timer-block-number">{timer.hours}</div>
                <div className="timer-block-value">hours</div>
            </div>
            <div className="timer-block">
                <div id="countdown-minutes" className="timer-block-number">{timer.minutes}</div>
                <div className="timer-block-value">minutes</div>
            </div>
            <div className="timer-block">
                <div id="countdown-seconds" className="timer-block-number">{timer.seconds}</div>
                <div className="timer-block-value">seconds</div>
            </div>
        </>
    );
}