import {Suspense, lazy, useState} from 'react';
import { createBrowserRouter , BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

import WindowProvider from './contexts/WindowProvider';
import HomePage from './pages/HomePage';
import MenuPage from './pages/MenuPage';
import AboutPage from './pages/AboutPage';

export default function App() {
    return (
        <>
            <BrowserRouter /*future={{ v7_startTransition: true }}*/>
                <WindowProvider>
                    <Routes>
                        <Route path="/firstsipmatcha">
                            <Route path="" element={<HomePage />} />
                            <Route path="menu" element={<MenuPage />} />
                            <Route path="about" element={<AboutPage />} />
                            <Route path="*" element={<Navigate to="/firstsipmatcha" />} />
                        </Route>
                    </Routes>
                </WindowProvider>
            </BrowserRouter>
        </>
    );
}