import { useState, useLayoutEffect, useEffect } from 'react';
import { getWindowContext } from '../contexts/WindowProvider';
import { NavLink } from 'react-router-dom';
import PageLayout from './PageLayout';
import Figure from '../components/Figure';

var win;
export default function AboutPage(){
    win = getWindowContext();

    // useEffect(() => {
    //     win.updateTitle("About Us | First Sip Matcha");
    // }, []);

    return(
        <PageLayout>
            <div id="about" className="page" style={{'paddingTop' : win.headerHeight}}>
                <div className="page-title">
                    <h1>About Us</h1>
                </div>
                <div className="page-body">
                    <section id="section-1" className="section">
                        <div className="section-heading"><h2>All about matcha</h2></div>
                        <div className="section-body">
                            <p>Matcha is a powdered form of Japanese green tea produced from specially grown and processed Camellia sinensis leaves.</p>
                            <p>It is made through 3 separate stages:</p>
                            <ol className="stage-container restrict">
                                <li className="stage">
                                    <div className="label">Cultivation</div>
                                    <div className="description">Tea bushes are shaded for several weeks before harvest, reducing sunlight exposure. This increases the chlorophyll and amino acid content, giving matcha its vibrant green color and unique flavor.</div>
                                </li>
                                <li className="stage">
                                    <div className="label">Harvest</div>
                                    <div className="description">Only the high-grade young tea leaves are selected. Stem, veins, and other impurities are removed in this process.</div>
                                </li>
                                <li className="stage">
                                    <div className="label">Processing</div>
                                    <div className="description">Leaves are then immediately steamed to prevent oxidation, dried, and then stone-ground into a fine powder.</div>
                                </li>
                            </ol>
                        </div>
                    </section>

                    <section id="section-2" className="section">
                        <div className="section-heading"><h2>First Sip Matcha Bar</h2></div>
                        <div className="section-body">
                            <Figure className = {"graphic"} figcaption={"figcaption"}>
                                <img src="" alt="alt text"/>
                            </Figure>
                            
                            <p>Just a bored 23 year old living at home and drinks matcha everyday so she decided to make a business out of it to escape working in academics and become filthy rich.</p>
                        </div>
                    </section>
                </div>
            </div>
        </PageLayout>
    );
}